import React from 'react'

export default function WhyUsCard({item}:{item: {id: number; title: string; content: string}}) {
  return (
    <div className="col-lg-4 at-4 at-lg-0">
        <div className='box' data-aos="zoom-in" data-aos-delay="200">
            <span>0{item.id}</span>
            <h4>{item.title}</h4>
            <p>{item.content}</p>
        </div>
    </div>
  );
}
